0%;\n}\n\n/* clears the X from Internet Explorer */\ninput[type=search]::-ms-clear {\n  display: none;\n  width: 0;\n  height: 0;\n}\n\ninput[type=search]::-ms-reveal {\n  display: none;\n  width: 0;\n  height: 0;\n}\n\n/* clears the X from Chrome */\ninput[type=\"search\"]::-webkit-search-decoration,\ninput[type=\"search\"]::-webkit-search-cancel-button,\ninput[type=\"search\"]::-webkit-search-results-button,\ninput[type=\"search\"]::-webkit-search-results-decoration {\n  display: none;\n}\n\n.hidden {\n  position: absolute !important;\n  width: 1px !important;\n  height: 1px !important;\n  padding: 0 !important;\n  margin: -1px !important;\n  overflow: hidden !important;\n  clip: rect(0, 0, 0, 0) !important;\n  white-space: nowrap !important;\n  border: 0 !important;\n}\n\n.hide {\n  display: none;\n}\n\nsection {\n  padding: 0 24px;\n\n  @include media(max-content-width) {\n    padding: 0;\n  }\n}\n\n.line-clamp {\n  display: -webkit-box;\n  -webkit-line-clamp: 6;\n  overflow: hidden;\n  -webkit-box-orient: vertical;\n}","@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@700&family=Lato:wght@400;700&family=Roboto&display=swap');\n\n$font-base: 'Lato', sans-serif;\n$font-title: 'DM Sans', sans-serif;\n$font-description: 'Roboto', sans-serif;\n","// header\n$title-color: #D04F4F;\n\n// search\n$ingredients-color: #3282F7;\n$appareils-color: #68D9A4;\n$utensils-color: #ED6454;\n\n// cards\n$card-img-background-color: #C7BEBE;\n\n// all\n$grey-background-color: #E7E7E7;\n$black: #000000;\n$white: #FFFFFF;\n","// flex\n@mixin flex($direction, $justify, $align) {\n  display: flex;\n  flex-direction: $direction;\n  justify-content: $justify;\n  align-items: $align;\n}\n\n// media queries\n@mixin media($breakpoint) {\n  @if $breakpoint == phone {\n    @media only screen and (max-width: 767px) {\n      @content;\n    }\n  }\n  @if $breakpoint == tablet {\n    @media only screen and (min-width: 768px) and (max-width: 1023px) {\n      @content;\n    }\n  }\n  @if $breakpoint == desktop {\n    @media only screen and (min-width: 1024px) {\n      @content;\n    }\n  }\n  @if $breakpoint == max-content-width {\n    @media only screen and (min-width: $max-content-width + 48px) {\n      @content;\n    }\n  }\n}","@import '../global/helpers';\n\n\n.filters__container filters-component {\n  @include flex(row, flex-start, center);\n  gap: 20px;\n\n  .custom__select {\n    display: flex;\n    flex-direction: column;\n    padding: 23px 10px 23px 19px;\n    border-radius: 5px;\n    cursor: pointer;\n    min-width: 170px;\n    position: relative;\n    transition: all 0.2s ease-out;\n    width: 0;\n\n    &:hover {\n      filter: brightness(1.08);\n      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);\n    }\n\n    &.open {\n      width: 62%;\n\n      .custom__select-arrow {\n        transform: rotate(180deg);\n      }\n\n      .filters__container__content {\n        width: 100%;\n        box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);\n\n      }\n\n      &:hover {\n        filter: none;\n      }\n    }\n\n    &[data-value=\"ingredients\"] {\n      background-color: $ingredients-color;\n\n      .filters__container__content {\n        background-color: $ingredients-color;\n      }\n    }\n\n    &[data-value=\"appliance\"] {\n      background-color: $appareils-color;\n\n      .filters__container__content {\n        background-color: $appareils-color;\n      }\n    }\n\n    &[data-value=\"utensils\"] {\n      background-color: $utensils-color;\n\n      .filters__container__content {\n        background-color: $utensils-color;\n      }\n    }\n\n    .input__container {\n      @include flex(row, space-between, center);\n      position: relative;\n      gap: 10px;\n    }\n\n    input {\n      cursor: pointer;\n      border: none;\n      background: inherit;\n      font-weight: 700;\n      font-size: 18px;\n      line-height: 22px;\n      text-transform: capitalize;\n      color: $white;\n      outline: none;\n\n      &::placeholder {\n        text-transform: none;\n        font-weight: 700;\n        font-size: 18px;\n        line-height: 22px;\n        color: #FFFFFF;\n        opacity: 0.5;\n      }\n    }\n\n    .custom__select-arrow {\n      background: url(../assets/arrow-d.svg) no-repeat center;\n      width: 20px;\n      height: 20px;\n      transition: transform .3s ease-in-out;\n    }\n\n    .filters__container__content {\n      position: absolute;\n      padding: 10px 5px 10px 19px;\n      border-radius: 5px;\n      top: 92%;\n      left: 0;\n    }\n\n    .filters__container__content__list {\n      ul {\n        display: grid;\n        grid-template-columns: repeat(3, 1fr);\n        grid-gap: 10px;\n        margin: 0;\n        list-style: none;\n        max-height: 350px;\n        overflow-y: auto;\n\n        &::-webkit-scrollbar {\n          width: 5px;\n        }\n\n        &::-webkit-scrollbar-track {\n          border-radius: 5px;\n          background: #f1f1f1;\n        }\n\n        &::-webkit-scrollbar-thumb {\n          border-radius: 5px;\n          background: #888;\n        }\n\n        &::-webkit-scrollbar-thumb:hover {\n          background: #555;\n        }\n\n        li p {\n          font-weight: 400;\n          font-size: 18px;\n          line-height: 22px;\n          color: #FFFFFF;\n          position: relative;\n          display: inline;\n          margin-bottom: 1px;\n\n          background-image: linear-gradient(currentColor, currentColor);\n          background-position: 0 99%;\n          background-repeat: no-repeat;\n          background-size: 0 1px;\n          transition: background-size 0.3s;\n\n          &:hover {\n            background-size: 100% 1px;\n          }\n        }\n      }\n    }\n  }\n\n}","@import '../global/helpers';\n\n.cards__container {\n  display: flex;\n  flex-wrap: wrap;\n  margin: 25px auto;\n  gap: 25px;\n\n  @include media(desktop) {\n    gap: 50px;\n  }\n}\n\ncard-component {\n  background: $grey-background-color;\n  border-radius: 5px;\n  min-width: 350px;\n  width: 100%;\n\n  @include media(desktop) {\n    width: calc((100% / 3) - 34px);\n  }\n\n  .card__image-container {\n    background: $card-img-background-color;\n    height: 178px;\n    border-radius: 5px 5px 0 0;\n  }\n\n  .card__content-container {\n    border-radius: 5px;\n    padding: 20px;\n\n    .card__header {\n      @include flex(row, space-between, center);\n      margin-bottom: 20px;\n\n      .card__header__title {\n        font-style: normal;\n        font-weight: 400;\n        font-size: 18px;\n        line-height: 22px;\n      }\n\n      .recipe__time {\n        @include flex(row, space-between, center);\n        gap: 5px;\n        font-weight: 700;\n        font-size: 18px;\n        line-height: 22px;\n      }\n    }\n  }\n\n  .card__content {\n    display: flex;\n    gap: 10px;\n\n    .ingredients__container {\n      width: 50%;\n\n      li {\n        font-weight: 400;\n        font-size: 12px;\n        line-height: 14px;\n        color: #000000;\n      }\n    }\n\n    .description__container {\n      width: 50%;\n      font-family: $font-description;\n      font-weight: 400;\n      font-size: 12px;\n      line-height: 100%;\n      color: #000000;\n    }\n  }\n}\n\n","@import '../global/_helpers';\n\nsearch-component .search__container {\n  margin-bottom: 25px;\n  width: 100%;\n  position: relative;\n\n  .search__button {\n    content: '';\n    position: absolute;\n    top: 50%;\n    right: 37px;\n    transform: translateY(-50%);\n    width: 26px;\n    height: 26px;\n    background-color: transparent;\n    background-image: url('../assets/search.svg');\n    background-repeat: no-repeat;\n    background-position: center;\n    background-size: cover;\n    border: none;\n    cursor: pointer;\n    transition: all 0.3s ease-in-out;\n  }\n\n  input[type=\"search\"] {\n    width: 100%;\n    height: 69px;\n    padding: 24px;\n    border-radius: 5px;\n    border: none;\n    font-weight: 400;\n    font-size: 18px;\n    line-height: 22px;\n    color: $black;\n    background-color: $grey-background-color;\n    transition: all 0.3s ease-in-out;\n\n    &:focus {\n      outline: none;\n      box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);\n\n      + .search__button {\n        transform: translateY(-50%) rotate(360deg);\n      }\n\n      &::placeholder {\n        color: transparent;\n      }\n    }\n  }\n}","@import '../global/helpers';\n\n.tags__container {\n  display: flex;\n  flex-wrap: wrap;\n  margin-bottom: 15px;\n  min-height: 40px;\n  gap: 10px;\n\n  .tag {\n    @include flex(row, center, center);\n    border-radius: 5px;\n    padding: 10px 20px;\n    gap: 13px;\n    color: $white;\n    font-weight: 700;\n    font-size: 14px;\n    line-height: 17px;\n    background-color: $grey-background-color;\n\n    &[data-tag-family=\"ingredients\"] {\n      background-color: $ingredients-color;\n    }\n\n    &[data-tag-family=\"appliance\"] {\n      background-color: $appareils-color;\n    }\n\n    &[data-tag-family=\"utensils\"] {\n      background-color: $utensils-color;\n    }\n\n    .remove {\n      width: 20px;\n      height: 20px;\n      border: none;\n      cursor: pointer;\n      background: url(../assets/remove.svg) no-repeat center;\n      transition: all 0.2s ease-out;\n\n      &:hover {\n        transform: scale(1.1);\n        background-image: url(\"data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_51_0)'%3E%3Cpath d='M14.59 8L12 10.59L9.41 8L8 9.41L10.59 12L8 14.59L9.41 16L12 13.41L14.59 16L16 14.59L13.41 12L16 9.41L14.59 8ZM12 2C6.47 2 2 6.47 2 12C2 17.53 6.47 22 12 22C17.53 22 22 17.53 22 12C22 6.47 17.53 2 12 2ZM12 20C7.59 20 4 16.41 4 12C4 7.59 7.59 4 12 4C16.41 4 20 7.59 20 12C20 16.41 16.41 20 12 20Z' fill='%23E7E7E7'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_51_0'%3E%3Crect width='24' height='24' fill='%23E7E7E7'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E%0A\");\n      }\n    }\n\n    &:not(:last-child) {\n      margin-right: 0.5rem;\n    }\n\n    &:hover {\n      filter: brightness(1.06);\n    }\n  }\n}","@import '../global/helpers';\n\nheader {\n  @include flex(column, center, center);\n  margin: 42px 0 17px;\n}"],"sourceRoot":""}]);
